@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Comic+Neue:wght@400&family=Edu+SA+Beginner&family=Poppins:wght@300&display=swap');

*{
    /* outline: 1px solid red; */
    font-family: 'Comic Neue';
    
    /* font-family: 'Poppins'; */
}

@layer base{
    button{
        @apply rounded-3xl text-white font-medium p-2 bg-orange-400
    }

}
@layer utilities {

    /* Chrome, Safari and Opera */
    .no-scrollbar::-webkit-scrollbar {
        display: none;
    }

    .no-scrollbar {
        -ms-overflow-style: none;
        /* IE and Edge */
        scrollbar-width: none;
        /* Firefox */
    }
}
